.Header
{
    display: block;
    height: auto;
    padding-bottom: 20px;

    background: #19082c;
}

.InfoHeader
{
    float: right;
}
.CatBlock
{
    width:110px;
    height: 130px;
    background: #fff;
    border-radius: 22px;
    margin-left:15px;
    box-shadow: 5px 5px 5px rgba(0, 0, 0, .25);
}

.CatItemText
{
    font-weight: 600;
    font-size: 14px;
}

.BlockName
{
    margin: 5px;
}

.TitleBlock
{
    font-weight: 600;
    font-size:20px;
}

.AboutBlock
{
    margin-top: 1px;
    font-size:14px;
    color: #B9B9B9;
}
:root {
    --background_page: #19082c;
    --header_background: #19082c;
}

.Container
{
    display: block;
    height: auto;

   

    background: #ffffff;

    border-top-left-radius: 20px;
    border-top-right-radius: 20px;
}
